*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: rgb(14, 84, 159);
  text-decoration: none;
}
.App {
  display: grid;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  grid-template-areas: 'header' 'fretboard' 'content';
  grid-template-rows: auto auto 1fr;
  grid-template-columns: 1fr;
  grid-gap: 24px; }

.App-header {
  display: grid;
  grid-area: header;
  grid-template: auto / auto 1fr auto;
  background: #F0F0F0;
  grid-column-gap: 16px;
  align-items: center;
  padding: 0 12px;
  border-bottom: 1px solid #DDD; }

.App-title {
  grid-area: 1 / 1;
  font-size: inherit;
  align-self: center; }

.App-nav {
  grid-area: 1 / 2;
  display: flex; }

.App-mode {
  grid-area: 1 / 1 / 1 / 2; }

.Explorer, .Quiz {
  grid-area: content;
  min-height: 0; }

.App-nav a {
  padding: 12px;
  text-align: center;
  border-left: 1px solid #CCC;
  width: 100px; }
  .App-nav a:last-child {
    border-right: 1px solid #CCC; }
  .App-nav a.active {
    background-color: #F8f8f8; }

.App-nav a.active {
  font-weight: bold; }

.Fretboard {
  grid-area: fretboard;
  display: grid;
  margin: 0 12px; }

.Fretboard-fingerboard {
  background: -webkit-linear-gradient(top, #493b19 0%, #331d0c 100%);
  background: linear-gradient(to bottom, #493b19 0%, #331d0c 100%); }

.Fretboard-head {
  background-color: #222;
  border-right: 4px solid #DDD; }

.Fret {
  border-right: 3px solid #CCC;
  position: relative; }
  .Fret .dot {
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #888;
    position: absolute;
    left: calc(50% - 10px); }
  .Fret .dot.single {
    top: calc(50% - 10px); }
  .Fret .dot.double1 {
    top: calc(25% - 10px); }
  .Fret .dot.double2 {
    top: calc(75% - 10px); }

.Fret-number {
  display: block;
  justify-self: center;
  margin: 6px; }

.Note {
   z-index: 20;
  display: block;
  align-self: center;
  justify-self: center;
  border-radius: 8px;
  height: 30px;
  padding: 0 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.Note sub {
  font-size: x-small;
}

.Note-quiz {
  background-color: #00fdff;
  z-index: 30;
}

.Note-indicator {
  background-color: #75ced8;
}

.Note-chord-root {
  background-color: rgb(66, 201, 97);
}
.Quiz-question {
  display: flex;
  flex-direction: column;
  align-items: center; }

.Quiz-answer-field {
  width: 30vw;
  font-size: 24px;
  padding: 12px;
  border: 1px solid #CCC;
  border-radius: 8px; }

@-webkit-keyframes bgToGray {
  to {
    background-color: lightgray; } }

@keyframes bgToGray {
  to {
    background-color: lightgray; } }

.Quiz-judgement {
  -webkit-animation-name: bgToGray;
          animation-name: bgToGray;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  margin-top: 24px;
  padding: 16px;
  font-size: 24px; }

.Quiz-judgement.correct {
  background-color: green; }

.Quiz-judgement.incorrect {
  background-color: red; }

.GuitarStrings-root-note {
  padding: 0 6px 0 12px;
  align-self: center; }
  .GuitarStrings-root-note.excluded {
    color: #CCC; }

.GuitarStrings-string {
  z-index: 10; }

.Listbox {
  overflow-y: auto;
  position: relative;
  padding: 0;
  border: 1px solid #DDD;
  background-color: #FFF;
  font-size: 12px;
  margin: 0; }
  .Listbox [role="option"] {
    display: block;
    padding: 0.4em 1em 0.4em 1em;
    position: relative;
    line-height: 1.8em; }
  .Listbox [role="option"].selected {
    background: #bde4ff; }

.Explorer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr auto;
  grid-template-rows: auto 1fr;
  grid-gap: 12px;
  margin: 0 12px 12px 12px; }

.Explorer-label {
  grid-row: 1;
  font-size: small;
  font-weight: bold; }

.Explorer-list {
  grid-row: 2; }

.Explorer-root {
  grid-area: 1; }

.Explorer-chord-type {
  grid-column: 2; }

.Explorer-bass-note {
  grid-column: 3; }

.Explorer-voicings {
  grid-column: 4; }

.Explorer-voicings-nav {
  grid-area: 2 / 5;
  display: flex;
  flex-direction: column; }
  .Explorer-voicings-nav > button {
    flex: 1 1; }

.Settings {
  grid-area: 1 / 3;
  justify-self: start;
  display: flex;
  font-size: small; }

.Settings-show-octave {
  grid-area: 2 / 4 / 2 / 4; }

.Settings-tuning {
  margin: 0 18px 0 6px; }

